import React, { useState, useEffect } from 'react';
import { Input, Select, List, Card, Spin } from 'antd';
import { useNavigate, useLocation } from 'react-router-dom';
import { searchParking } from '../services/parkingService';

const { Search } = Input;
const { Option } = Select;

const ParkingSearch = () => {
  const [searchResults, setSearchResults] = useState([]);
  const [searchType, setSearchType] = useState('name');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    //  URL 
    const searchParams = new URLSearchParams(location.search);
    const keyword = searchParams.get('keyword');
    if (keyword) {
      handleSearch(keyword);
    }
  }, [location.search]);

  const handleSearch = async (value) => {
    setLoading(true);
    setError(null);
    try {
      const results = await searchParking(searchType, value);
      setSearchResults(results);
    } catch (error) {
      console.error('', error);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  const handleParkingClick = (parkingId) => {
    navigate(`/parking/${parkingId}`);
  };

  return (
    <div className="search-container">
      <div className="search-header">
        <Select 
          defaultValue="name" 
          style={{ width: 120 }} 
          onChange={value => setSearchType(value)}
        >
          <Option value="name"></Option>
          <Option value="address"></Option>
          <Option value="price"></Option>
          <Option value="availability"></Option>
        </Select>
        <Search
          placeholder=""
          allowClear
          enterButton=""
          size="large"
          onSearch={handleSearch}
          style={{ width: 400, marginLeft: 10 }}
          loading={loading}
        />
      </div>

      {error && (
        <div className="error-message" style={{ color: 'red', margin: '10px 0' }}>
          {error}
        </div>
      )}

      {loading ? (
        <div className="loading-spinner">
          <Spin size="large" />
        </div>
      ) : (
        <List
          grid={{ gutter: 16, column: 3 }}
          dataSource={searchResults}
          renderItem={item => (
            <List.Item onClick={() => handleParkingClick(item.id)}>
              <Card 
                hoverable
                title={item.name}
                style={{ marginTop: 16 }}
              >
                <p>{item.address}</p>
                <p>{item.price}/</p>
                <p>{item.availableSpots}</p>
              </Card>
            </List.Item>
          )}
        />
      )}
    </div>
  );
};

export default ParkingSearch; 